<template>
    <div class="container">
        <div class="main__info">
            <div class="main__info-left">
                <div class="user__info">
                    <img src="@/assets/images/profilePFP.png" alt="" class="UserPFP">
                    <h1>{{ getUser.name }}'s information:</h1>
                    <p>Name: {{ getUser.name }}</p>
                    <p>Username: {{ getUser.username }}</p>
                    <p>Email: {{ getUser.email }}</p>
                    <h2>Address:</h2>
                    <p>City: {{ getUser.address.city }}</p>
                    <p>Street: {{ getUser.address.street }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                currentId: null
            }
        },
        created() {
            this.currentId = this.$route.params.id
            this.getUsers()
        },
        computed: {
            users() {
                return this.$store.state.users
            },
            getUser() {
                return this.users.find((info) => info.id == this.currentId)
            }
        },
        methods: {
            getUsers() {
                this.$store.dispatch('getUsers') 
            }
        }
    }
</script>
